body {
  height: 100vh;
  background: #222 0 0 / cover no-repeat;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 120%;
  display: flex;
}

.card {
  color: #fff;
  width: 100%;
  max-width: 420px;
  background: #000000d0;
  border-radius: 30px;
  margin: 1em;
  padding: 2em;
}

.search {
  justify-content: left;
  align-items: center;
  display: flex;
}

button {
  height: 44px;
  width: 44px;
  color: #fff;
  cursor: pointer;
  background: #7c7c7c2b;
  border: none;
  border-radius: 50%;
  outline: none;
  margin: .5em;
  transition: all .2s ease-in-out;
}

input.search-bar {
  color: #fff;
  width: calc(80% - 100px);
  background: #7c7c7c2b;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: .4em 1em;
  font-family: inherit;
  font-size: 105%;
}

button:hover, .toggle-btn:hover {
  cursor: pointer;
  background: #7c7c7c6b;
}

.toggle-btn {
  height: 35px;
  width: 70px;
  background: #7c7c7c2b;
  border-radius: 24px;
  padding: 5px;
  transition: all .3s ease-in-out;
}

.toggle-btn > .inner-circle {
  width: 35px;
  height: 35px;
  background: #fff;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  position: relative;
}

.inner-circle > h5 {
  color: gray;
  text-align: center;
  margin: 0;
  font-size: 1.25em;
  position: absolute;
  left: 10%;
}

.toggle-btn.active {
  background: #00acee;
}

.toggle-btn.active:hover {
  background: #008bc2;
}

.toggle-btn.active > .inner-circle {
  margin-left: 35px;
}

h1.temp {
  margin: 0 0 .4em;
}

.flex {
  align-items: center;
  display: flex;
}

.loading {
  visibility: hidden;
  max-height: 20px;
  position: relative;
}

.loading:after {
  visibility: visible;
  content: "Loading...";
  color: #fff;
  position: absolute;
  top: 0;
}

.description {
  text-transform: capitalize;
  margin-left: 8px;
}

